import "../CSS/Titlebar.css";
import Button from "./Button";
import { styled } from '@mui/material/styles';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch, { SwitchProps } from '@mui/material/Switch';
import SwapVertIcon from "@mui/icons-material/SwapVert";
import Tooltip from '@mui/material/Tooltip';
import { ThemeProvider, createTheme } from '@mui/material/styles';

type TitlebarProps = {
  title: string
  back: boolean

}

const theme = createTheme({
  typography: {
    allVariants: {
      fontFamily: 'Poppins',
      // textTransform: 'none',
      // fontSize: 16,
    },
  },
});

export const Titlebar = (props: TitlebarProps) => {
    const IOSSwitch = styled((props: SwitchProps) => (
        <Switch focusVisibleClassName=".Mui-focusVisible" disableRipple {...props} />
      ))(({ theme }) => ({
        width: 42,
        height: 26,
        padding: 0,
        '& .MuiSwitch-switchBase': {
          padding: 0,
          margin: 2,
          transitionDuration: '300ms',
          '&.Mui-checked': {
            transform: 'translateX(16px)',
            color: '#fff',
            '& + .MuiSwitch-track': {
              backgroundColor: theme.palette.mode === 'dark' ? '#2ECA45' : '#4fbc9c',
              opacity: 1,
              border: 0,
            },
            '&.Mui-disabled + .MuiSwitch-track': {
              opacity: 0.5,
            },
          },
          '&.Mui-focusVisible .MuiSwitch-thumb': {
            color: '#33cf4d',
            border: '6px solid #fff',
          },
          '&.Mui-disabled .MuiSwitch-thumb': {
            color:
              theme.palette.mode === 'light'
                ? theme.palette.grey[100]
                : theme.palette.grey[600],
          },
          '&.Mui-disabled + .MuiSwitch-track': {
            opacity: theme.palette.mode === 'light' ? 0.7 : 0.3,
          },
        },
        '& .MuiSwitch-thumb': {
          boxSizing: 'border-box',
          width: 22,
          height: 22,
        },
        '& .MuiSwitch-track': {
          borderRadius: 26 / 2,
          backgroundColor: theme.palette.mode === 'light' ? '#E9E9EA' : '#39393D',
          opacity: 1,
          transition: theme.transitions.create(['background-color'], {
            duration: 500,
          }),
        },
      }));

    return( 
    <div className="titlebar">
        {
          (props.back) ? <div className="Title"><Button style="Back" icon="back" />{props.title}</div> : <div className="Title">{props.title}</div>
        }
        
        <div className="actions">
        {
          (props.back) ? 
          <div className="toggleGRP">
            <ThemeProvider theme={theme}><Tooltip title="Sort Cases by Date" placement="top" arrow><div className="togglespace"><FormControlLabel control={<IOSSwitch sx={{ m: 1 }} defaultChecked />} label="Date" /><SwapVertIcon className="sortHover" /></div></Tooltip></ThemeProvider> 
            <ThemeProvider theme={theme}><Tooltip title="Sort Cases by Severity" placement="top" arrow><div className="togglespace"><FormControlLabel control={<IOSSwitch sx={{ m: 1 }} />} label="Severity" /><SwapVertIcon className="sortHover" /></div></Tooltip></ThemeProvider> 
            <ThemeProvider theme={theme}><Tooltip title="Show Draft Cases" placement="top" arrow><div className="togglespace"><FormControlLabel control={<IOSSwitch sx={{ m: 1 }} />} label="Draft" /></div></Tooltip></ThemeProvider> 
          </div> : ""
        }          
            <div className="actionGRP">
              <Button style="Alt-Ghost" icon="snapshot" />
              <Button style="Alt-Ghost" icon="close" />
              <Button style="Alt-Ghost" icon="grid" />
              <Button Label="Add New Case" style="Default" icon="plus" />
            </div>
        </div>

    </div> )
}