import "../CSS/Button.css";
import AccountCircleTwoToneIcon from "@mui/icons-material/AccountCircleTwoTone";
import ChevronRightTwoToneIcon from "@mui/icons-material/ChevronRightTwoTone";
import PowerSettingsNewIcon from "@mui/icons-material/PowerSettingsNew";
import AddIcon from '@mui/icons-material/Add';
import GridViewIcon from '@mui/icons-material/GridView';
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted';
import DoDisturbOnOutlinedIcon from '@mui/icons-material/DoDisturbOnOutlined';
import ManageHistoryOutlinedIcon from '@mui/icons-material/ManageHistoryOutlined';
import Tooltip from '@mui/material/Tooltip';
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";
import LightbulbIcon from "@mui/icons-material/Lightbulb";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import CancelIcon from "@mui/icons-material/Cancel";
import DraftsIcon from "@mui/icons-material/Drafts";
import EditIcon from "@mui/icons-material/Edit";
import CloseIcon from "@mui/icons-material/Close";

type ButtonProps = {
  Label?: string
  style?: string
  icon?: string
}

export const Button = (props: ButtonProps) => {
  switch(props.icon) {
    
    case "Draft": return (<div><button className={props.style}><DraftsIcon className="icon transp"/><div>{props.Label}</div></button></div>)
    case "Canceled": return (<div><button className={props.style}><CancelIcon className="icon transp"/><div>{props.Label}</div></button></div>)
    case "Complete": return (<div><button className={props.style}><CheckCircleIcon className="icon transp"/><div>{props.Label}</div></button></div>)
    case "Moderate": return (<div><button className={props.style}><ErrorOutlineIcon className="icon transp"/><div>{props.Label}</div></button></div>)
    case "Active": return (<div><button className={props.style}><LightbulbIcon className="icon transp"/><div>{props.Label}</div></button></div>)
    
    case "Critical": return (<div><button className={props.style}><ErrorOutlineIcon className="icon transp"/><div>{props.Label}</div></button></div>)
    case "edit": return (<div><button className={props.style}><EditIcon className="icon transp"/>{props.Label}</button></div>)
    case "closed": return (<div><button className={props.style}>{props.Label}<CloseIcon className="icon transp"/></button></div>)
    case "right": return (<div><button className={props.style}>{props.Label}<ChevronRightTwoToneIcon className="transp"/></button></div>)
    case "back": return (<div><button className={props.style}>{props.Label}<ArrowBackIosIcon className="icon transp"/></button></div>)
    case "power": return (<div><button className={props.style}>{props.Label}<PowerSettingsNewIcon className="transp"/></button></div>)
    case "plus": return (<div><button className={props.style}><AddIcon className="transp"/>{props.Label}</button></div>)
    case "grid": return (<div><Tooltip title="Grid View" placement="top" arrow><button className={props.style} style={{width:"24px"}}><GridViewIcon className="transp"/>{props.Label}</button></Tooltip></div>)
    case "list": return (<div><Tooltip title="List View" placement="top" arrow><button className={props.style} style={{width:"24px"}}><FormatListBulletedIcon className="transp"/>{props.Label}</button></Tooltip></div>)
    case "close": return (<div><Tooltip title="Closed Cases" placement="top" arrow><button className={props.style} style={{width:"24px"}}><DoDisturbOnOutlinedIcon className="transp"/>{props.Label}</button></Tooltip></div>)
    case "snapshot": return (<div><Tooltip title="Snapshot" placement="top" arrow><button className={props.style} style={{width:"24px"}}><ManageHistoryOutlinedIcon className="transp"/>{props.Label}</button></Tooltip>
</div>)
    
    default: return (<div><button className="Default">{props.Label}</button></div>)
  }
}
export default Button;
