import Button from "./Button";
import "../CSS/Card.css";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import EditIcon from "@mui/icons-material/Edit";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";
import LightbulbIcon from "@mui/icons-material/Lightbulb";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import CancelIcon from "@mui/icons-material/Cancel";
import DraftsIcon from "@mui/icons-material/Drafts";

import { Routes, Route, Outlet, Link } from "react-router-dom";

export const Card = () => {
  const CaseSeverity = "Critical";

  return (
    <div className="patient-card">
      <div className="patient-name">
        <div className="ActualName">
          {"lastName"}, {"firstName"}
        </div>
        <div className="Severity">
              <Button Label="Active" style="Active" icon="Active" /> 
              {/* <Button Label="Critical" style="Critical" icon="Critical" /> 
              <Button Label="Moderate" style="Moderate" icon="Moderate" /> 
              <Button Label="Complete" style="Complete" icon="Complete" /> 
              <Button Label="Canceled" style="Canceled" icon="Canceled" /> 
              <Button Label="Draft" style="Draft" icon="Draft" />  */}
          <div className="GenderAge">
            {"Female"}, {"60"} years
          </div>
        </div>
      </div>

      <div className="documents">
        <div className="line1">{"Transition Care Management"}</div>
        <div className="line2">{"Last Update: March 28, 2024, 10:40 AM"}</div>
      </div>

      <div className="button-section">
        <div style={{ width: "100%", display: "inline-block" }}>
        <Button Label="Details" style="Default" icon="right" />
        </div>

        <div style={{ width: "auto", display: "inline-block" }}>
        <Button Label="Edit" style="Ghost" icon="edit" />
        </div>
      </div>
    </div>
  );
}

export default Card;
